import React, { useState, Fragment, useEffect } from 'react'
import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import { Button } from '@material-ui/core';
import EditIcon from '@material-ui/icons/Edit';
import DeleteIcon from '@material-ui/icons/DeleteOutlined';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Slide from '@material-ui/core/Slide';
import RestoreIcon from '@material-ui/icons/Restore';
import CalibersForm from "./CalibersForm";

import Tooltip from '@material-ui/core/Tooltip';
import TablePagination from '@material-ui/core/TablePagination';
import { listCaliberTable, deleteCaliber } from '../../api/api';

const useStyles = makeStyles(theme => ({
    root: {
        flexGrow: 1,
    },
    iconWidth: {
        width: 10
    },
    paper: {
        padding: theme.spacing(2),
        marginTop: 10,
    },
    button: {
        margin: theme.spacing(1),
    },
    input: {
        display: 'none',
    },
    fab: {
        margin: theme.spacing(1),
    },
    extendedIcon: {
        marginRight: theme.spacing(1),
    },
    icon: {
        marginRight: theme.spacing(1),
        color: 'black'
    },
}))

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
})



const Caliber = () => {

    const classes = useStyles()

    const [calibers, setCalibers] = useState([])
    const [totalCalibers, setTotalCalibers] = useState(0)
    const [rowsPerPage, setRowsPerPage] = useState(10);
    const [page, setPage] = useState(0);

    const [open, setOpen] = React.useState(false);
    const [openRestoreCaliber, setOpenRestoreCaliber] = React.useState(false);
    const [openEditCaliber, setOpenEditCaliber] = React.useState(false);
    const [openCreateCaliberForm, setOpenCreateCaliberForm] = React.useState(false);
    const [caliber, setCaliber] = useState({})


    useEffect(() => {
        let data = { start: 0, end: rowsPerPage, paginate: true }
        listCaliberTable(data).then(response => {
            setCalibers(response.calibersTable);
            setTotalCalibers(response.count);
        });
    }, []);

    const onOpenDeleteCaliber = caliber => () => {
        setCaliber(caliber)
        setOpen(true);
    }
    const onOpenRestoreCaliber = caliber => () => {
        setCaliber(caliber)
        setOpenRestoreCaliber(true);
    }
    const onCloseDeleteCaliber = () => {
        setOpen(false);
        let data = { start: 0, end: rowsPerPage }
        listCaliberTable(data).then(response => {
            setCalibers(response.calibersTable);
            setTotalCalibers(response.count);
        });
    }
    const onDeleteCaliber = () => {
        setOpen(false);
        deleteCaliber({ id: caliber._id }).then(response => {
            if (response.status === 'success') {
                const newCalibers = calibers.filter(currentCaliber => caliber._id !== currentCaliber._id);
                setCalibers(newCalibers)
            }
        });
    }

    const onEditCaliber = caliber => () => {
        setCaliber(caliber)
        setOpenEditCaliber(true)
    };
    const onCloseEditCaliber = () => {
        setOpenEditCaliber(false)
    };
    const onCloseRestoreCaliber = () => {
        setOpenRestoreCaliber(false)
    };
    const onCreateItem = () => {
        setOpenCreateCaliberForm(true)
    }
    const onCloseCaliberForm = () => {
        setOpenCreateCaliberForm(false)
    }
    const passCaliberCreate = caliber => {
        setCalibers(calibers => [...calibers, caliber]);
        setOpenCreateCaliberForm(false);
    }
    const passCaliberEdit = caliber => {
        setOpenEditCaliber(false);
        const newCalibers = calibers.map((currentCalibers) => {

            if (caliber._id !== currentCalibers._id)
                return currentCalibers

            currentCalibers = caliber;
            return currentCalibers
        })
        setCalibers(newCalibers)
    }
    const handleChangePage = (event, newPage) => {
        setPage(newPage);
        let data = { start: (newPage * rowsPerPage), end: rowsPerPage, paginate: true }
        listCaliberTable(data).then(response => {
            setCalibers(response.calibersTable);
            setTotalCalibers(response.count);
        });
    }
    const handleChangeRowsPerPage = event => {

        setRowsPerPage(+event.target.value);
        setPage(0);
        let data = { start: 0, end: event.target.value, paginate: true }
        listCaliberTable(data).then(response => {
            setCalibers(response.calibersTable);
            setTotalCalibers(response.count);
        });

    }

    return (
        <Fragment>
            <Paper className={classes.paper}>
                <Button variant="contained" color="primary" onClick={onCreateItem}>Crear item</Button>

                <Table>
                    <TableHead>
                        <TableRow>
                            <TableCell>Calibre</TableCell>
                            <TableCell>Capacidad</TableCell>
                            <TableCell>Protección</TableCell>
                            <TableCell>Tipo</TableCell>
                            <TableCell colSpan={3}>Acciones</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {
                            calibers.map(caliber => {
                                return (
                                    <TableRow key={caliber._id}>
                                        <TableCell>{caliber.caliber}</TableCell>
                                        <TableCell>{caliber.amps}</TableCell>
                                        <TableCell>{caliber.protection}</TableCell>
                                        <TableCell>{caliber.type === 'cooper' ? 'Cobre' : 'Aluminio'}</TableCell>

                                        <TableCell className={classes.iconWidth}>
                                            <Tooltip title="Editar" placement="top">
                                                <EditIcon className={classes.icon} onClick={onEditCaliber(caliber)} />
                                            </Tooltip>
                                        </TableCell>
                                        <TableCell className={classes.iconWidth}>
                                            <Tooltip title="Eliminar" placement="top">
                                                <DeleteIcon className={classes.icon} onClick={onOpenDeleteCaliber(caliber)} />
                                            </Tooltip>
                                        </TableCell>

                                    </TableRow>)
                            }
                            )}
                    </TableBody>
                </Table>
                <TablePagination
                    rowsPerPageOptions={[10, 25, 100]}
                    component="div"
                    count={totalCalibers}
                    rowsPerPage={rowsPerPage}
                    page={page}
                    onChangePage={handleChangePage}
                    onChangeRowsPerPage={handleChangeRowsPerPage}
                />
            </Paper>
            <Dialog
                open={open}
                onClose={onCloseDeleteCaliber}
                aria-labelledby="draggable-dialog-title">
                <DialogTitle style={{ cursor: 'move' }} id="draggable-dialog-title">
                    Alerta
                </DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        Esta seguro de eliminar este item?
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button autoFocus onClick={onCloseDeleteCaliber} color="primary">
                        NO
                    </Button>
                    <Button onClick={onDeleteCaliber} color="primary">
                        SI
                    </Button>
                </DialogActions>
            </Dialog>



            <Dialog maxWidth="md" open={openEditCaliber} onClose={onCloseEditCaliber} TransitionComponent={Transition}>
                <DialogContent>
                    <CalibersForm handleClose={onCloseEditCaliber} caliber={caliber} passCaliberEdit={passCaliberEdit} />
                </DialogContent>
                <DialogActions>
                    <Button autoFocus onClick={onCloseEditCaliber} color="primary">
                        Cerrar
                    </Button>
                </DialogActions>
            </Dialog>

            <Dialog open={openCreateCaliberForm} onClose={onCloseCaliberForm} >
                <DialogTitle id="form-dialog-title">Crear rol</DialogTitle>
                <DialogContent>
                    <CalibersForm caliber={false} handleClose={onCloseCaliberForm} passCaliberCreate={passCaliberCreate} />
                </DialogContent>
                <DialogActions>
                    <Button onClick={onCloseCaliberForm} color="primary">
                        Cerrar
                    </Button>
                </DialogActions>
            </Dialog>
        </Fragment>
    )
}

export default Caliber