import React, { Fragment, useEffect, useState } from 'react'
import { emphasize, makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import Container from '@material-ui/core/Container';
import MenuItem from "@material-ui/core/MenuItem";

import clsx from 'clsx';

import { createCaliber, updateCaliber, listCaliberTable, listCalibersType } from '../../api/api';

import { Button } from '@material-ui/core';
import TextField from "@material-ui/core/TextField";
import Swal from 'sweetalert2';

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
    height: 250,
    minWidth: 290
  },
  input: {
    display: "flex",
    padding: 0,
    height: "auto"
  },
  valueContainer: {
    display: "flex",
    flexWrap: "wrap",
    flex: 1,
    alignItems: "center",
    overflow: "hidden"
  },
  margin: {
    marginTop: 10
  },
  chip: {
    margin: theme.spacing(0.5, 0.25)
  },
  chipFocused: {
    backgroundColor: emphasize(
      theme.palette.type === "light"
        ? theme.palette.grey[300]
        : theme.palette.grey[700],
      0.08
    )
  },
  noOptionsMessage: {
    padding: theme.spacing(1, 2)
  },
  singleValue: {
    fontSize: 16
  },
  placeholder: {
    position: "absolute",
    left: 2,
    bottom: 6,
    fontSize: 16
  },

  divider: {
    height: theme.spacing(2)
  },
  paper: {
    padding: theme.spacing(2),
    color: theme.palette.text.secondary,

  },
  button:
  {
    margin: theme.spacing(1),
  },
  fab:
  {
    marginTop: -2,
    float: "right"
  },
  extendedIcon: {
    marginRight: theme.spacing(1),
  },
  formControl: {
    margin: theme.spacing(1),
  },
}));


const CalibersForm = props => {

  const [calibersType, setCalibersType] = useState([])
  const classes = useStyles()
  const [values, setValues] = React.useState({
    caliber: '',
    amps: '',
    protection: '',
    caliberType: ''
  })

  useEffect(() => {
    if (props.caliber) {
      setValues({
        ...values,
        caliber: props.caliber.caliber,
        amps: props.caliber.amps,
        protection: props.caliber.protection,
        caliberType: props.caliber.type,
      })
    }
    listCalibersType().then(response => {
      setCalibersType(response.calibersType)
    })
  }, []);

  const initialState = {
    caliber: '',
    amps: '',
    protection: '',
    caliberType: '',
  }


  const inputChange = prop => event => {
    setValues({ ...values, [prop]: event.target.value })
  }

  const onSubmit = e => {

    if (!props.caliber) {
      createCaliber(values).then(response => {
        if (response.status === 'success') {
          props.handleClose()
          props.passCaliberCreate(response.caliber)
          Swal.fire({
            icon: 'success',
            title: `Registrado exitosamente`,
            showConfirmButton: false,
            timer: 1500
          });

          setValues({ ...initialState })
        }
      });
    } else {
      values.id = props.caliber._id;
      updateCaliber(values).then(response => {
        if (response.status === 'success') {
          props.handleClose()
          props.passCaliberEdit(response.caliber)

          Swal.fire({
            icon: 'success',
            title: `Actualizado exitosamente`,
            showConfirmButton: false,
            timer: 1500
          })

          setValues({ ...initialState });
        }
      });
    }
  }


  return (
    <Fragment>

      <Container maxWidth="md" className={classes.margin}>

        <Paper className={classes.paper}>
          <form onSubmit={e => {
            e.preventDefault()
            onSubmit()
          }} >
            <Grid container spacing={3}  >
              <Grid item xs={6}>
                <TextField fullWidth
                  autoComplete='off'
                  className={clsx(classes.margin, classes.textField)}
                  variant="outlined"
                  label="Calibre"
                  value={values.caliber}
                  onChange={inputChange('caliber')} />
              </Grid>

              <Grid item xs={6}>
                <TextField fullWidth
                  autoComplete='off'
                  className={clsx(classes.margin, classes.textField)}
                  variant="outlined"
                  label="Amperios"
                  type='number'
                  value={values.amps}
                  onChange={inputChange('amps')} />
              </Grid>

              <Grid item xs={6}>
                <TextField fullWidth
                  autoComplete='off'
                  className={clsx(classes.margin, classes.textField)}
                  variant="outlined"
                  label="Protección"
                  type='number'
                  value={values.protection}
                  onChange={inputChange('protection')} />
              </Grid>

              <Grid item sm={6}>
                <TextField
                  select
                  fullWidth
                  variant="outlined"
                  margin="dense"
                  label="Tipo"
                  value={values.caliberType}
                  onChange={inputChange('caliberType')}>
                  {
                    calibersType.map(option => (
                      <MenuItem key={option._id} value={option._id} >
                        {option.name}
                      </MenuItem>))
                  }
                </TextField>
              </Grid>


              <Grid item xs={6}>
                <Button type="submit" variant="contained" color='primary'>{!props.caliber ? 'Crear' : 'Actualizar'}</Button>
              </Grid>
            </Grid>
          </form>
        </Paper>

      </Container>
    </Fragment>
  )
}

export default CalibersForm